	import wPicker from '@/components/w-picker/w-picker.vue';
	import md5 from '../../static/js/md5.js';
	import rs from '../../static/js/request.js';
	let {
		log
	} = console;
	const app = getApp().globalData;
	const {
		appid,
		appsecret,
		imgRemote
	} = app;

	export default {
		components: {
			wPicker
		},
		data() {
			return {
				display: true,
				title: '',
				contact: '',
				mobile: '',
				address: '',
				childzid: '',
				childListProps: {
					label: 'nickname',
					value: 'zid'
				},
				childList: [{
					zid: '',
					nickname: '当前账号'
				}],
				deliveryListProps: {
					label: 'delivery_time_info',
					value: 'delivery_time_id'
				},
				deliveryList: [{
					delivery_time_id: '',
					delivery_time_info: '不限'
				}],
				couponsListProps: {
					label: 'txt',
					value: 'id'
				},
				couponsList: [{
					id: '',
					txt: '不使用'
				}],
				deliveryId: '',
				deliveryTime: '不限',
				account: '当前账号',
				cash: '不使用',
				couponsId: '',
				juanPrice: 0,
				sendDate: '',
				startyear: '',
				cartInfo: {},
				reduce: '',
				page: 1,
				num: 10,
				loading: false,
				showTop: false,
				config: [],
				cartware: [],
				itemList: [],
				remark:'',
				gift:0
			};
		},
		methods: {
			shoplist() {
				uni.navigateTo({
					url: "/pages/shopcart/shoplist"
				})
			},
			deliveryPage() {
				uni.navigateTo({
					url: '/pages/shopcart/delivery'
				});
			},
			collectBill() {
				uni.switchTab({
					url: '/pages/tabar/index'
				})
			},
			childInfo() {
				let timeStamp = Math.round(new Date().getTime() / 1000);
				let obj = {
					appid: appid,
					timeStamp: timeStamp
				};
				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
					sign: sign
				}, obj)
				rs.getRequests("childInfo", params, (res) => {
					let data = res.data;
					if (data.code == 200) {
						this.childList.push(...data.data)
					}
				})
			},
			newArr(arr) {
				return arr.map(function(val, ind) {
					return {
						id: val.id,
						txt: '',
					}
				});
			},
			addInfo() {
				let timeStamp = Math.round(new Date().getTime() / 1000);
				let obj = {
					appid: appid,
					timeStamp: timeStamp
				};
				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
					sign: sign
				}, obj)
				rs.getRequests("addInfo", params, (res) => {
					let {
						data
					} = res;
					this.cartInfo = data.data;
					let {
						userInfo,
						activity_rule,
						totalPrice,
						activity_type
					} = data.data;

					this.contact = userInfo.contact;
					this.mobile = userInfo.phone;
					this.address = userInfo.address;
					// 判断购物车是否有数量
					if (data.data.countNum == 0 && this.page == 1) {
						this.indexItem()
					}
					// 配送时间
					this.deliveryList.push(...data.data.delivery_time_list);

					// 现金券
					if (data.data.couponsList.length != 0) {
						let nrr = this.newArr(data.data.couponsList)
						for (let i = 0; i < data.data.couponsList.length; i++) {
							nrr[i].txt = `${data.data.couponsList[i].coupons_title}(余额${data.data.couponsList[i].residue}元)`
						}
						this.couponsList.push(...nrr);
					}
					// 满赠
					if(activity_type==2){
						if(data.data.fullPrice>=activity_rule[0].price){
							this.gift=1
						}else{
							this.gift=0;
						}
					}
					// 满减
					let length = activity_rule.length - 1;
					if (activity_type == 1) {
						if (activity_rule[length].price <= totalPrice) {
							this.reduce =
								`已享受满<text style='color: #FF3E1E;'>${activity_rule[length].price}元</text>减<text style='color: #FF3E1E;'>${activity_rule[length].reduce}元<text>`;

						} else {
							for (let i of activity_rule) {
								if (totalPrice < i.price) {
									this.reduce =
										`再满<text style='color: #FF3E1E;'>${i.price - totalPrice}元</text>减<text style='color: #FF3E1E;'>${i.reduce}元</text>`;
									return;
								}
							}
						}
					}
				})
			},
			// 显示子账号
			selectAccount() {
				this.$refs.account.show();
			},
			onConfirmAccount(e) {
				log(e);
				if (!e.value) {
					let {
						userInfo
					} = this.cartInfo;
					this.address = userInfo.address;
					this.contact = userInfo.contact;
					this.mobile = userInfo.mobile;
				} else {
					this.address = e.obj.address;
					this.contact = e.obj.contact;
					this.mobile = e.obj.mobile;
				}
				this.childzid = e.value;
				this.account = e.result;
			},
			// 显示配送日期
			selectDate() {
				this.$refs.date.show();
			},
			onConfirmDate(e) {
				let {
					year,
					month,
					day
				} = e.obj;
				this.sendDate = `${year}-${month}-${day}`;
			},
			// 显示配送时间
			selectTime() {
				this.$refs.delivery.show();
			},
			onConfirmDelivery(e) {
				log(e);
				this.deliveryTime = e.result;
				this.deliveryId = e.value;
			},
			// 显示现金券
			selectCash() {
				this.$refs.cash.show();
			},
			onConfirmCash(e) {
				this.cash = e.result;
				this.couponsId = e.value;
				this.juanPrice = 0;
				log(e)
				if (e.obj.id != '') {
					let timeStamp = Math.round(new Date().getTime() / 1000);
					let obj = {
						appid: appid,
						timeStamp: timeStamp,
						id: e.obj.id
					};

					let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
					let params = Object.assign({
						sign: sign,
					}, obj)
					rs.getRequests("useCouponsPrice", params, (res) => {
						if (res.data.code == 200) {
							this.juanPrice = res.data.data.couponsPrice;
						} else {
							rs.Toast(res.data.msg)
						}
					})
				}
			},
			//确认下单
			confirmOrder() {
				let timeStamp = Math.round(new Date().getTime() / 1000);
				let obj = {
					appid: appid,
					timeStamp: timeStamp,
					send_time: this.sendDate
				};

				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
					sign: sign,
				}, obj)
				var that = this;
				rs.getRequests("testOrder", params, (res) => {
					let code = res.data.code;
					let data = res.data.data;
					if (data.on_delivery == 1) {
						switch (code) {
							case 101:
								uni.showModal({
									title: '提示',
									content: '直接下单？',
									success: function(res) {
										if (res.confirm) {
											that.order();
										}
									}
								});
								break;
							case 300:

								uni.showModal({
									title: '提示',
									content: '信用金即将用完，请及时结账（可以直接下单）？',
									success: function(res) {
										if (res.confirm) {
											that.order();
										}
									}
								});

								break;
							case 301:
								uni.showModal({
									title: '提示',
									content: '信用金即将用完，请及时结账（可以合并订单）？',
									success: function(res) {
										if (res.confirm) {
											that.mergeOrder();
										} else if (res.cancel) {
											uni.showModal({
												title: '提示',
												content: '直接下单？',
												success: function(res) {
													if (res.confirm) {
														that.order();
													}
												}
											});
										}
									}
								});
								break;

							case 500:
								Toast({
									message: '信用金已用完，不能下单',
									duration: 1200
								});
								break;

							case 200:

								uni.showModal({
									title: '提示',
									content: '可以合并订单？',
									success: function(res) {
										if (res.confirm) {
											that.mergeOrder();
										} else if (res.cancel) {
											uni.showModal({
												title: '提示',
												content: '直接下单？',
												success: function(res) {
													if (res.confirm) {
														that.order();
													}
												}
											});
										}
									}
								});
								break;
						}
					} else {
						uni.showModal({
							title: '提示',
							content: '直接下单？',
							success: function(res) {
								if (res.confirm) {
									that.order();
								}
							}
						});
					}
				})
			},
			// 合拼下单
			mergeOrder() {
				let timeStamp = Math.round(new Date().getTime() / 1000);
				var date = this.dateOfTime;
				// way 1公众号 2App 3小程序 下单方式







				     let way=3;

				let obj = {
					appid: appid,
					timeStamp: timeStamp,
					send_time: date,
					way: way
				};
				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
					sign: sign,
				}, obj)
				rs.postRequests('mergeOrder', params, (res) => {
					if (res.data.code != 200) {
						rs.Toast(res.data.msg)
					} else {
						rs.Toast('合拼订单成功');
						uni.switchTab({
							url: '/pages/tabar/order',
						})
					}
				})
			},
			// 直接下单
			order() {
				let timeStamp = Math.round(new Date().getTime() / 1000);
				var date = this.sendDate;
				var select_zid = this.childzid;
				var timearea = this.deliveryTime;
				var timeId = this.deliveryId;
				var remark = this.remark.replace(/\s/g,'');
				let timeInfo = '';
				if (timearea == "不限") {
					timeInfo = '';
				} else {
					timeInfo = timearea;
				}







				     let way=3;

				let obj = {
					appid: appid,
					timeStamp: timeStamp,
					send_time: date,
					way: way
				};
				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
					delivery_time_id: timeId,
					delivery_time_info: timeInfo,
					sign: sign,
					remark: remark,
					coupons_member_id: this.cartInfo.on_delivery == 0 ? this.cashId : 0,
					select_zid: select_zid
				}, obj);

				rs.postRequests('addOrder', params, (res) => {
					let data = res.data;

					if (data.code == 104) {
						rs.Toast('超出下单时间，不能下单');
						return false;
					} else if (data.code == 200) {
						if (data.data.on_delivery == 0) {
							let id = data.data.orderId;
							uni.navigateTo({
								url: `/pages/shopcart/pay?id=${id}`,
							})
							return;
						}
						rs.Toast('下单成功');
						uni.switchTab({
							url: '/pages/tabar/order',
						})
					} else if (data.code == 500) {
						rs.Toast('服务器内部错误,请稍候再试')
					} else if(data.code==407){
						rs.Toast('超出活动限制,请删除部分商品');
					}else{
						rs.Toast(data.msg)
					}
				})
			},
			indexItem() {
				let timeStamp = Math.round(new Date().getTime() / 1000);
				let obj = {
					appid: appid,
					timeStamp: timeStamp
				};
				let {
					num,
					page
				} = this;

				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
						page: 1,
						sign: sign,
						page: page,
						num: num
					},
					obj
				);

				rs.getRequests('indexItem', params, res => {
					let data = res.data;
					if (data.code == 200) {
						this.itemList = data.data.list;
						this.config = data.data;
						if (data.data.total <= 10) {
							this.loading = false;
						} else {
							this.loading = true;
						}
					}
				});
			},
			openCart(item) {
				this.$refs.popup.open();
				this.cartware = item;
			},
			onClose() {
				this.$refs.popup.close();
			},
			getSendTime(){
				var date = new Date();
				let n = date.getTime() + 24 * 3600000;
				date.setTime(n);
				var year = date.getFullYear();
				var month = date.getMonth() + 1;
				var day = date.getDate();
				this.startyear = year;
				if (month < 10) {
					month = '0' + month;
				}
				if (day < 10) {
					day = '0' + day;
				}
				this.sendDate = `${year}-${month}-${day}`;	
		},
		onShow() {
			if (uni.getStorageSync("is_child") != 1) {
				this.childInfo();
			}
            this.getSendTime();
			this.addInfo();
		},
		onHide() {
			this.childzid = '';
			this.childList = [{
				zid: '',
				nickname: '当前账号'
			}];
			this.deliveryList = [{
				delivery_time_id: '',
				delivery_time_info: '不限'
			}];
			this.couponsList = [{
				id: '',
				txt: '不使用'
			}];
			this.deliveryId = '';
			this.deliveryTime = '不限';
			this.account = '当前账号';
			this.cash = '不使用';
			this.couponsId = '';
			this.juanPrice = 0;
			this.remark='';
		},
		onLoad() {
			uni.hideTabBar();
		},
		onReady(){
			uni.onWindowResize((res) => {
					this.display = !this.display;
				})
			}
		},
		
		onReachBottom() {
			//页面上拉触底事件的处理函数
			var that = this;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			let {
				num,
				page
			} = that;
			var obj = {
				appid: appid,
				timeStamp: timeStamp
			};

			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				num: num,
				page: page + 1,
				timeStamp: timeStamp,
				sign: sign
			};
			rs.getRequests('indexItem', data, res => {
				let {
					data
				} = res;
				if ((data.code = 200)) {
					if (data.data != '') {
						this.itemList.push(...data.data.list);
						this.page += 1;
						this.loading = true;
					} else {
						this.loading = false;
					}
				}
			});
		},
		onPageScroll(e) {
			if (e.scrollTop == 0) {
				this.showTop = false;
			} else {
				this.showTop = true;
			}
		}
	};
